const ContactFormRegisterStyled = styled(Grid)`
  &&.contact-form-register {
    padding: 10px;
    color: #000;
    background-color: #8bb7f9;

    .title {
      color: #f6ff00;
      text-align: center;
      font-weight: 700;
      font-style: normal;
      font-size: 22px;
      margin-top: 20px;
      margin-bottom: 10px;
    }
    .form-input {
      font-size: 15px;
    }
  }
`;

export default ContactFormRegister;
