import { LoaderFunctionArgs } from "react-router-dom";

const fetchData = async (productId?: string) => {
  try {
    const url = "/jsons/products/product-" + (productId || "all") + ".json";
    const response = await fetch(url);
    const jsonData = await response.json();
    return jsonData;
  } catch (error) {
    return [];
  }
};

export function getProduct(productId?: string) {
  return fetchData(productId);
}

interface ProductLoaderParam {
  productId: string;
}

export default async function loader({
  params,
}: LoaderFunctionArgs<ProductLoaderParam>) {
  const data = await getProduct(params.productId);
  return {
    ...data,
    listBreadcrumb: [{ name: "Ni mua nhc c", url: "/noi-mua-nhac-cu" }],
  };
}

export async function loaderAll() {
  return getProduct();
}
